.movie-list-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    justify-items: start;
    min-height: 100vh;
}


.movie-list{
    margin : 30px 0px 30px 0px;
    /* height: 100vh; */
}

.movie-list span{
    text-align: left;
}

.all-movies{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    column-gap: 20px;
    row-gap: 30px;
    margin: 30px 20px 0px 20px;
    padding: 3px 3px 3px 3px;

}

.navigation{
    max-width: 500px;
    margin: auto;
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.page-navigation{
    display: flex;
    flex-direction: row;
    gap: 15px;
}

.page-number{
    /* border: solid rgb(0, 255, 136) 2px; */
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 9px 3px rgb(0, 221, 255) inset;
    color: rgb(136, 0, 255);
    width: 65px;
    height: 65px;
    text-align: center;
    border-radius: 10px;
}

.page-hover{
    cursor: pointer;
    transition: ease-in-out transform 150ms;
}

.page-current {
    box-shadow: 0px 0px 9px 3px var(--light-green) inset;
}

.page-hover:hover{
    transform: scale(1.35);
}


@media (max-width : 1024px){
    .all-movies{
        grid-template-columns: repeat(4,1fr);
    }
}
@media (max-width : 768px){
    .all-movies{
        grid-template-columns: repeat(3,1fr);
    }
}
@media (max-width : 568px){
    .all-movies{
        grid-template-columns: repeat(2,1fr);
    }
}
@media (max-width : 420px){
    .all-movies{
        grid-template-columns: repeat(1,1fr);
    }
}

