.cards{
    display: flex;
    flex-direction: column;
    gap: 8px;
    box-shadow: 0px 0px 9px 3px var(--light-green) inset;
    padding: 20px 25px;
    border-radius: 8px;
    transition: ease-in-out transform 100ms;
}

.cards img{
    border-radius: 8px;
}

.cards span{
    text-transform: capitalize;
    font-size: var(--cards-font);
    font-weight: 800;
    color: black;
}

.cards:hover{
    transform: scale(1.1);
}

@media (max-width: 568px){
    :root{
        --cards-font: 15px;
    }
}
@media (max-width: 420px){
    :root{
        --cards-font: 13px;
    }
}
