@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css");


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --main-title : 60px;
    --sub-title : 40px;
    --cards-font : 19px;
    --category-font : 40px;
    --footer-font : 30px;
    --dark-blue : #032541;
    --light-green : #11d62b;
    --searchbar-font : 18px;
}

body {
    max-width: 100%;
    background-color: #fdf5e6;
}

a{
    text-decoration: none;
}

a:active{
    color: inherit;
}

.hidden{
    display: hide;
}

.container{
    max-width: 1120px;
    margin: auto;
    align-items: center;
}

.white{
    color: white;
}

.responsive-img{
    max-width: 100%;
    height: auto;
}

.main-title{
    font-size: var(--main-title);
    font-weight: 700;
}

.sub-title{
    font-size: var(--sub-title);
    font-weight: 600;
}

.label{
    font-weight: 600;
    font-size: 24px;
    color: rgb(189, 226, 129);

}
.sub{
    font-weight: 400;
    font-size: 22px;
    color: white;
}







@media (max-width: 1200px){
    .container{
        max-width: 970px;
    }
    .label{
        font-size: 22px;
    }
    .sub{
        font-size: 20px;
    }
}
@media (max-width: 1024px){
    .container{
        max-width: 740px;
    }
    .label{
        font-size: 20px;
        font-weight: 500;
    }
    .sub{
        font-size: 18px;
        font-weight: 300;
    }
}
@media (max-width: 768px){
    .container{
        max-width: 500px;
    }
}
@media (max-width: 568px){
    .container{
        max-width: 85%;
    }
}
