.section1{
    position: relative;
    height: 65vh;
}

.section1 img{
    position: absolute;
    width: 100%;
    height: 65vh;
    z-index: -1;
}

.section1-contents{
    color: var(--light-green);
    display: flex;
    flex-direction: column;
    padding-top: 50px;
}


@media (max-width: 1024px){
    :root{
        --main-title : 50px;
        --sub-title : 35px;
    }
}
@media (max-width: 768px){
    :root{
        --main-title : 40px;
        --sub-title : 30px;
    }
}
@media (max-width: 568px){
    :root{
        --main-title : 35px;
        --sub-title : 28px;
    }
}
@media (max-width: 420px){
    :root{
        --main-title : 30px;
        --sub-title : 24px;
    }
}