.Searchbar{
    top: 80px;
    width: 100%;
    position: relative;
}

.Searchbar input{
    width: 100%;
    height: 35px;
    border-radius: 15px;
    border: none;
    display: inline-block;
    padding-left: 10px;
    font-size: var(--searchbar-font);
}

.Searchbar input:focus{
    outline: 2px solid var(--light-green);
}

.Searchbar button{
    position: absolute;
    right: 0;
    height: 35px;
    width: 10%;
    border-radius: 15px;
    background-color: var(--light-green);
    border: none;
    cursor: pointer;
    font-size: var(--searchbar-font);
    font-weight: 700;
    color: var(--dark-blue);
    transition: linear background-color 50ms, scale 50ms;
}

.Searchbar button:active{
    background-color: rgb(9, 199, 88);
    scale: 1.005;
}

@media (max-width: 768px) {
    .Searchbar {
        top: 50px;
    }
    .Searchbar button{
        width: 15%;
    }
    :root{
        --searchbar-font : 14px;
    }
}

@media (max-width: 568px){
    .Searchbar button{
        width: 20%;
    }
    :root{
        --searchbar-font : 15px;
    }
}
@media (max-width: 568px){
    :root{
        --searchbar-font : 12px;
    }
}