.category{
    padding-bottom:  50px;
}

.category-font{
    font-size: var(--category-font);
}

.title-contents{
    padding-top: 40px;
    display: flex;
    flex-direction: row;
    gap: 50px;
    align-items: center;
}

.title-contents i{
    cursor: pointer;
    color: black;
}


.movie-cards{
    display: flex;
    flex-direction: row;
    margin: 30px 20px 0px 20px;
    gap: 20px;
    padding: 3px 3px 3px 3px;
}


@media (max-width: 568px){
    :root{
        --category-font: 25px;
    }
}
