.navbar{
    background-color: var(--dark-blue);
    height: 60px;   
    display: flex;
}

.navbar-contents{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.navbar-contents span{
    cursor: default;
    font-size: 30px;
    font-weight: 700;
}

.navbar-links{
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.navbar-links a{
    font-size: 20px;
    text-decoration: none;
    font-weight: 500;
    cursor: pointer;
    transition: linear transform 150ms, color 150ms;
}

.navbar-links a:hover {
    transform: translateY(1px);
    color: #00A4E4;
}





@media (max-width: 568px){
    .navbar-links a{
        font-size: 17px;
    }
    .navbar-contents span{
        font-size: 27px;
    }
}
@media (max-width: 420px){
    .navbar-links{
        gap: 8px
    }
    .navbar-links a{
        font-size: 15px;
    }
    .navbar-contents span{
        font-size: 22px;
    }
}
