.footerWrapper{
    background-color: var(--dark-blue);
    padding-bottom: 25px;
    padding-top: 25px;
}

.footer{
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    align-items: center;
}

.socials{
    display: flex;
    flex-direction: row;
    gap: 8px;
}

.socials a{
    transition: linear transform 110ms;
}

.socials i{
    font-size: var(--footer-font);
    font-weight: 600;
    color: white;
}

.footer a{
    width: 100%;
}

.tmdb{
    width: 20%;
    float: right;
    transition: ease-in-out transform 150ms;
}

.socials a:hover{
    transform: scale(1.25);
}

.tmdb:hover{
    transform: scale(1.25);
}

@media (max-width: 1024px){
    :root{
        --footer-font: 23px;
    }
}
@media (max-width: 768px){
    :root{
        --footer-font: 20px;
    }
    .tmdb{
        width: 30%;
    }
}
@media (max-width: 420px){
    :root{
        --footer-font: 18px;
    }
    .tmdb{
        width: 55%;
    }
}