.movie-section1{
    position: relative;
    height: 70vh;
}

.details-backdrop{
    position: absolute;
    width: 100%;
    height: 70vh;
    z-index: -1;
}

.movie-section1-content{
    display: grid;
    grid-template-columns: 25% 75%;
    height: 70vh;
}

.details-poster{
    max-height: 60vh;
    border-radius: 15px;
}

.movie-description{
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.movie-description span{
    text-align: justify;
}

.release-date{
    display: flex;
    flex-direction: row;
    gap: 20px;
}

@media (max-width: 1024px){
    .movie-description{
        gap: 2px;
    }
    .movie-section1-content{
        grid-template-columns: 25% 70%;
        gap: 40px;
    }
}
@media (max-width: 768px){
    .movie-section1-content{
        grid-template-columns: 100%;
        justify-items: center;
        padding-top: 20px;
    }
    .details-backdrop,
    .movie-section1{
        height: 150vh;
    }
}

@media (max-width: 420px){
    .details-backdrop,
    .movie-section1{
        height: 175vh;
    }
}






