.all-videos{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 80px;
    margin-bottom: 80px;
}

.video{
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 20px;
    width: 100%;
}

.video iframe{
    width: 60%;
    aspect-ratio: 16 / 9;
    border: none;
}

@media (max-width: 768px){
    .video iframe{
        width: 100%;
    }
}

